<template>
  <v-container>
   
    <v-card light hover text-xs-center wrap class="d-flex">
      <v-container  text-xs-center>
<v-layout row wrap>
        <v-flex d-flex xs2>
          <v-icon x-large dark left :color="color" @click="doSwitch" >wb_incandescent</v-icon>
        </v-flex>
        <v-flex d-flex xs10>
          <v-layout row wrap>
            <v-flex  d-flex xs12 >
              <div text-xs-center class="font-weight-bold">
              {{device.name}}
              </div>
            </v-flex>
            <v-flex d-flex>
              <v-layout row wrap>
                <v-flex d-flex xs12>
                  <v-btn flat large color="green" @click="switchOn">On</v-btn>
                  <v-btn flat large color="red" @click="switchOff">Off</v-btn>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
      </v-container>
    </v-card >
  </v-container>
</template>

<script>
  export default {
    props: ['device'],
    computed: {
    // a computed getter
    color: function () {
      switch(this.device.switch.value){
        case 1:
          return "#ffca00";
        case 0:
          return "#424242";
          default:
          return "#FFCDD2";
      }
    }
    },
    methods: {
    doSwitch: function () {
      var s = this.device.switch.value==1?0:1;
      this.$store.dispatch("sendMessage",{"type":"Switch","device":this.device.name,"switch":s})
    },
    switchOn:function () {
      this.$store.dispatch("sendMessage",{"type":"Switch","device":this.device.name,"switch":1})
    },
    switchOff:function () {
      this.$store.dispatch("sendMessage",{"type":"Switch","device":this.device.name,"switch":0})
    }

  }
  }
</script>

