import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import router from './router'
import VueNativeSock from 'vue-native-websocket'
import store from './store'

//Use the hostname from the url used to access the dasboard for the websocket url
var url = new URL("/", location);
var wsurl = (location.protocol=="https:"?"wss://"+url.hostname+":443":"ws://"+url.hostname+":80");
Vue.use(VueNativeSock, wsurl, { store: store, format: 'json',reconnection: true, // (Boolean) whether to reconnect automatically (false)
reconnectionAttempts: 5, // (Number) number of reconnection attempts before giving up (Infinity),
reconnectionDelay: 3000 })
Vue.config.productionTip = false
//Register a service worker so the app can load quicker, even without a connection
if ('serviceWorker' in navigator) {
  window.addEventListener('load', () => {
      navigator.serviceWorker.register('/service-worker.js').then(registration => {
          console.log('SW registered: ', registration);
      }).catch(registrationError => {
          console.log('SW registration failed: ', registrationError);
      });
  });
}

new Vue({
  router,store,
  render: h => h(App),
  data () {
    return {
      info: null
    }
  },
  mounted () {
    
  }
},).$mount('#app')
